.App {
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-image: url(https://images.unsplash.com/photo-1534308983496-4fabb1a015ee?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1510&q=80);
  background-size: cover;
}

.tags {
  width: 90%;
  margin: 5%;
  height: 80%;
  padding: 10px;
  background-color: #ffffffea;
  border-radius: 10px;
}

.header {
 display: flex;
 width: 100vw;
}

.category {
  flex: 1;
}

.copy {
  width:80px;
  background-color: #4a70ec;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 15px;
  font-weight: bold;
}

.copy:hover {
  background-color: #7996f5;
}

.copy:active {
  background-color: #b379f5;
}